import React from "react";
import Slider from "react-slick";
import './index.css'

export default function SimpleSlider() {

  return (
    <div className="sliderContainer">
      <Slider >
      <div>
        <h3>1</h3>
      </div>
      <div>
        <h3>2</h3>
      </div>
      <div>
        <h3>3</h3>
      </div>
      <div>
        <h3>4</h3>
      </div>
      <div>
        <h3>5</h3>
      </div>
      <div>
        <h3>6</h3>
      </div>
    </Slider>
    </div>
  );
}